import React from "react";

function CandidateStatus(props) {

    const { list, statusList } = props;
    const filterList = list.filter(t => statusList.includes(t.id));

    return (
        (filterList.length) ?
            filterList.map((t, i) => (
                <div className="candidate-status" id={t.id} key={i}>
                    <img alt={t.name} src={t.Image} />
                    <div>{t.name}</div>
                </div>
            )
            ) : null
    )

}
export default CandidateStatus;