import React from "react";
import Candidate from "./Candidate";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import CandidateStatus from "./CandidateStatus";

export default class CandidateDetails extends React.Component {

    state = {
        list: [],
        shortlisted: [],
        rejected: [],
        search: "",
        singleData: {}
    }

    componentDidMount() {
        fetch("https://s3-ap-southeast-1.amazonaws.com/he-public-data/users49b8675.json")
            .then(res => res.json())
            .then(list => this.setState({ list }))
    }

    handleChange = (ev) => {
        let value = ev.target.value;
        const name = ev.target.name;
        let filterList = this.state.list.filter(t => t.name.indexOf(value) > 0);
        this.setState({
            [name]: value,
            list: filterList
        })
    }

    handleShortlist = (value) => {
        let newShortlist = [...this.state.shortlisted, value];
        this.setState({ shortlisted: newShortlist })
    }

    handleRejected = (value) => {
        let newRejected = [...this.state.rejected, value];
        this.setState({ rejected: newRejected })
    }

    updateSingleData = (value) => {
        this.setState({ singleData: value })
    }

    render() {
        const { list, search, shortlisted, rejected, singleData } = this.state;
        return (
            <Router>

                <div className="status-btns">
                    <button><Link to="/">Home</Link></button>
                    <button><Link to="/shortlisted">Shortlisted</Link></button>
                    <button><Link to="/rejected">Rejected</Link></button>
                </div>

                <Switch>
                    <Route path="/shortlisted">
                        <div className="list-container">
                            <CandidateStatus list={list} statusList={shortlisted}></CandidateStatus>
                        </div>
                    </Route>

                    <Route path="/rejected">
                        <div className="list-container">
                            <CandidateStatus list={list} statusList={rejected}></CandidateStatus>
                        </div>
                    </Route>
                    <Route path="/:id" >
                        <Candidate data={singleData} handleShortlist={this.handleShortlist} handleRejected={this.handleRejected} isStatus />
                    </Route>

                    <Route path="/">
                        <div className="search-box">
                            <input type="search" value={search} name="search" onChange={this.handleChange} placeholder="search candidate" />
                        </div>
                        <div className="list-container">
                            {list.length ? list.map((t, i) => <Candidate key={i} data={t} handleShortlist={this.handleShortlist} handleRejected={this.handleRejected} updateSingleData={this.updateSingleData} />) : null}
                        </div>
                    </Route>
                </Switch>
            </Router>
        )
    }
}