import React from "react";
import { useHistory } from "react-router-dom";

function Candidate(props) {

    const handleShortlist = () => {
        const { data: { id }, handleShortlist, isStatus } = props
        handleShortlist(id);
        if (isStatus) {
            history.push("/");
        }
    }

    const handleRejected = () => {
        const { data: { id }, handleRejected, isStatus } = props
        handleRejected(id);
        if (isStatus) {
            history.push("/");
        }
    }
    const history = useHistory();

    const handleClick = () => {
        props.updateSingleData(props.data);
        history.push("/" + props.data.id);
    }

    const { data: { name, Image, id } } = props;
    return (
        <div className="candidate" id={id}>
            <img alt={name} src={Image} onClick={handleClick} />
            <div>{name}</div>

            <div>
                <button onClick={handleShortlist}>Shortlist</button>
                <button onClick={handleRejected}>Reject</button>
            </div>

        </div>
    )

}

export default Candidate;